import React, { Component } from 'react';
import {Modal, Select, Row, Col, Button, Input, Tooltip, Alert, Radio} from 'antd';
import Outlink from '../components/outlink';
const Option = Select.Option;

class YOptions extends Component {
		constructor(props){
	      super(props);
	      
	      this.state = {
	      		visible: false,
	      		outLinkShowFlag: false,
	      		yOptions: []
	      }
	      this.owner = this.props.owner;
	  }
	  
	  componentWillReceiveProps(nextProps) {
	  		let {visible, yOptions} = nextProps;
	  		this.setState({visible, yOptions});
	  }
	  
	  changeOption(index, e){
	  		let {yOptions} = this.state;
	  		yOptions[index]['name'] = e.target.value;
	  		this.setState({yOptions});
	  }
	  
	  changeOptionType(index, value){
	  		let {yOptions} = this.state;
	  		yOptions[index]['type'] = value;
	  		this.setState({yOptions});
	  }
	  
	  changeLinkType(index, e){
	  		let value = e.target.value;
	  		let {yOptions} = this.state;
	  		yOptions[index]['linkType'] = value;
	  		yOptions[index]['link'] = '';
	  		this.setState({yOptions});
	  }	  

	  changeOutLink(index){
	  		let {yOptions} = this.state;
	  		yOptions[index]['outLinkShowFlag'] = true;
	  		this.setState({yOptions, defaultValue: yOptions[index]['link'], outLinkShowFlag: yOptions[index]['outLinkShowFlag']});
	  }	

	  closeOutLink(index){
	  		let {yOptions} = this.state;
	  		delete yOptions[index]['outLinkShowFlag'];
	  		this.setState({yOptions, outLinkShowFlag: false});
	  }	
	  	  
	  changeLink(index, value){
	  		let {yOptions} = this.state;
	  		yOptions[index]['link'] = value;
	  		delete yOptions[index]['outLinkShowFlag'];
	  		this.setState({yOptions});
	  }	
	  	  
	  handleOk(){
	  		this.owner.setState({yOptions: this.state.yOptions, yOptionsVisible: false});
	  }
	  
	  handleCancel(){
	  		this.owner.setState({yOptionsVisible: false});
	  }
	  
	  render() {  		
		    let {yOptions} = this.state;
		    let chartProperty = this.owner;
		    let reportOption = chartProperty.owner;
		    let tdExcel = reportOption.owner;
		    let main = tdExcel.owner;
		    
		    return (
		      <div>
		        <Modal title="系列选项" visible={this.state.visible} onOk={this.handleOk.bind(this)} onCancel={this.handleCancel.bind(this)} footer={[
		            <Button key="back" type="ghost" onClick={this.handleCancel.bind(this)}>返 回</Button>,
		            <Button key="submit" type="primary" onClick={this.handleOk.bind(this)}>
		              确 定
		            </Button>,
	          ]} width="800px" style={{ top: 20 }}>
		        		{
		        				yOptions.map((option, index) => (
		        						<Row key={index} style={{'margin-bottom': '2px'}}>
				        						<Col span={5}>
				        								<Input value={option.name} onChange={this.changeOption.bind(this, index)} readOnly/>
				        						</Col>
				        						<Col span={4}>
				      									<Select value={option.type} onChange={this.changeOptionType.bind(this, index)} style={{width:'120px'}}>
				      											<Option value="" key=""></Option>
														      	<Option value="column" key="column">柱状图</Option>
														      	<Option value="stackedColumn" key="stackedColumn">栈柱状图</Option>
														      	<Option value="bar" key="bar">条状图</Option>
														      	<Option value="stackedBar" key="stackedBar">栈条状图</Option>
														      	<Option value="line" key="line">折线图</Option>
														      	<Option value="pie" key="pie">饼状图</Option>
														      	<Option value="area" key="area">面积图</Option>
														      	<Option value="stackedArea" key="stackedArea">栈面积图</Option>
														      	<Option value="funnel" key="funnel">烟囱图</Option>
														      	<Option value="radar" key="radar">雷达图</Option>
														      	<Option value="gauge" key="gauge">仪表盘</Option>
														    </Select>
												    </Col>
												    <Col span={6}>
												    	<Radio.Group defaultValue="0" value={option.linkType ? option.linkType : '0'} buttonStyle="solid" onChange={this.changeLinkType.bind(this, index)}>
							            				<Radio.Button value="0">链接表单</Radio.Button>
							            				<Radio.Button value="1">链接地址</Radio.Button>
							            		</Radio.Group>								          	
												    </Col>
												    <Col span={9}>
							            		{
							            		option.linkType !== '1' ?
									            <div className="config-link-default-outlink config-default-outlink" onClick={()=>this.changeOutLink(index)}>设置链接</div> : 
									            <div>
									              <Tooltip title="链接地址以{字段名称}方式引用表单字段值，例如：/general/hr/manage/query/show_staff_info.php?USER_ID={登录名}，其中登录名代表字段">
									              <input type="text" placeholder="请输入链接地址" className="config-text-classname ant-input" value={option.link} onChange={e => this.changeLink(index, e.target.value)}/>
									              </Tooltip>
									            </div>
									          	}
									            {
									          option.outLinkShowFlag ? <Outlink {...main.props} owner={this} noTarget={true} updateOutlink={(def) => this.changeLink(index, def)} closeOutLink={()=>this.closeOutLink(index)}/> : null
									          	}													    
												    </Col>
										    </Row>		        						
		        				))
		        		}
		        </Modal>
	        </div>
	      )
		}	  
}

export default YOptions;