    add = () => {
        let {sheets} = this.state;
        sheets.push({
            index: "" + (sheets.length + 1),
            name: '工作表' + (sheets.length + 1),
            cellSettings: {},
            cellMetas: {}
        })
        let activekey = "" + sheets.length;
        this.setState({sheets, activekey});
    }

    remove = (targetKey) => {
        let activeKey = this.state.activeKey;
        let lastIndex;
        this.state.sheets.forEach((sheet, i) => {
            if (sheet.index === targetKey) {
                lastIndex = i - 1;
            }
        });
        const sheets = this.state.sheets.filter(sheet => sheet.index !== targetKey);
        if (lastIndex >= 0 && activeKey === targetKey) {
            activeKey = sheets[lastIndex].index;
        }
        this.setState({sheets, activeKey});
    }

    op(editIndex, key) {
        switch (key) {
            case "1":
                this.setState({editSheetName: true, editIndex});
                break;
            case "2":
                if (this.state.sheets.length <= 1) {
                    alert('至少要有一个工作表！');
                } else {
                    if (window.confirm('确定要删除该工作表吗？')) {
                        let {activeKey} = this.state;
                        const sheets = this.state.sheets.filter(sheet => sheet.index !== editIndex);
                        sheets.map(sheet => {
                            if (parseInt(sheet.index) >= parseInt(editIndex)) {
                                sheet.index = '' + (parseInt(sheet.index) - 1);
                            }
                        })
                        if (activeKey === editIndex) {
                            if (sheets.length < parseInt(editIndex)) {
                                activeKey = '' + sheets.length;
                            }
                        }
                        window.setTimeout(() => this.setState({
                            sheets,
                            activeKey,
                            hoverIndex: '',
                            sheetOpShowFlag: false,
                            sheetColorShowFlag: false
                        }), 0);
                    }
                }
                break;
            case "3":
                let {sheets} = this.state;
                sheets.push({"index": '' + (sheets.length + 1)});
                for (let i = sheets.length - 2; i >= parseInt(editIndex) - 1; i--) {
                    for (let j in sheets[i]) {
                        if (j !== "index") {
                            sheets[i + 1][j] = sheets[i][j];
                        }
                    }
                }
                sheets[parseInt(editIndex)]["name"] += ' 副本';
                this.setState({sheets});
                break;
            case "4":
                this.setState({moveSheetShowFlag: true, editIndex});
                break;
            case "5":
                this.setState({sheetColorShowFlag: true});
                break;
        }
    }

    changeSheetName = (e) => {
        let {sheets, editIndex} = this.state;
        sheets.map(sheet => {
            if (sheet.index == editIndex) {
                sheet.name = e.target.value;
            }
        });
        this.setState({sheets});
    }

    endChangeSheetName = () => {
        this.setState({editSheetName: false, editIndex: ""});
    }

    sheetColorChange(sheetIndex, color) {
        let {sheets} = this.state;
        sheets.map(sheet => {
            if (sheet.index == sheetIndex) {
                sheet.color = color.hex;
            }
        });
        this.setState({sheets});
    }

    sheetColorClose() {
        this.setState({sheetColorShowFlag: false});
    }

    move() {
        let {sheets, moveIndex, editIndex, moveType} = this.state;
        let theSheet, theIndex;//待移动工作表位置

        let begin = 0;//移动后的新位置
        sheets.map((sheet, index) => {
            if (sheet.index == moveIndex) {
                if (moveType == 1) {
                    begin = index;
                } else {
                    begin = index + 1;
                    if (begin >= sheets.length) {
                        begin = sheets.length - 1;
                    }
                }
            }
            if (sheet.index == editIndex) {
                theSheet = sheet;
                theIndex = index;
            }
        });


        if (begin != theIndex) {//位置变了？
            for (let i = theIndex; i < sheets.length - 1; i++) {//先把待移动的工作表放到最后，以便只考虑往前移动的情况
                sheets[i] = sheets[i + 1];
            }

            sheets[sheets.length - 1] = theSheet;

            sheets.map((sheet, index) => {//重新确定相关位置
                if (sheet.index == moveIndex) {
                    if (moveType == 1) {
                        begin = index;
                    } else {
                        begin = index + 1;
                        if (begin >= sheets.length) {
                            begin = sheets.length - 1;
                        }
                    }
                }
                if (sheet.index == editIndex) {
                    theSheet = sheet;
                    theIndex = index;
                }
            });

            //现在只有往前移动一种情况
            if (begin != theIndex) {
                for (let i = sheets.length - 2; i >= begin; i--) {
                    sheets[i + 1] = sheets[i];
                }
                sheets[begin] = theSheet;
            }
            //重新编工作表index
            sheets.map((sheet, index) => sheet.index = '' + (index + 1));
            //console.log(sheets);
            window.setTimeout(() => this.setState({
                sheets,
                activeKey: theSheet.index,
                editIndex: "",
                hoverIndex: "",
                moveSheetShowFlag: false,
                moveIndex: "",
                moveType: "1"
            }), 0);
        }
    }

    toPos(visualOffset, pos, lengths){
        let res = visualOffset;
        while(res <= lengths.length && lengths[res - 1] > pos){
            pos -= lengths[res - 1];
            res++;
        }
        if(res > lengths.length){
            res = lengths;
        }
        return res;
    }

