        cellSettings["afterSelectionEnd"] = function(row, column, row2, column2, preventScrolling, selectionLayerLevel) {
            if(me.props.setForm === undefined) return;
            let meta = this.getCellMeta(row, column), cell = this.getCell(row, column);
            if(meta.def == undefined){
                meta.def = {};
            }
            let def = {...meta.def}
            def.color = $(cell).css('color')
            def.bgColor = $(cell).css('background-color')
            def.fontSize = $(cell).css('font-size') ? "" + parseInt($(cell).css('font-size').replace("px", "")) : 12
            def.fontName = $(cell).css('font-family')
            def.bold = $(cell).css('font-weight') > 400 ? true : false
            def.italic = $(cell).css('font-style') == "italic"
            def.underLine = $(cell).css('text-decoration') ? $(cell).css('text-decoration').indexOf("underline") != -1 : false
            def.lineThrough = $(cell).css('text-decoration') ? $(cell).css('text-decoration').indexOf("line-through") != -1 : false
            if($(cell).hasClass('htLeft')){
                def.align = 'htLeft'
            }else if($(cell).hasClass('htRight')){
                def.align = 'htRight'
            }else if($(cell).hasClass('htCenter')){
                def.align = 'htCenter'
            }
            if($(cell).hasClass('htTop')){
                def.verticalAlign = 'htTop'
            }else if($(cell).hasClass('htBottom')){
                def.verticalAlign = 'htBottom'
            }else if($(cell).hasClass('htMiddle')){
                def.verticalAlign = 'htMiddle'
            }
            def.wordWrap = meta['wordWrap']
            let settings = this.getSettings()
            //let sheets = me.changeSheet(-1);
            me.setState({curDef: def, pageType: settings["pageType"] ? settings["pageType"] : "", pageWidth: settings["pageWidth"] ? settings["pageWidth"] : "", pageHeight: settings["pageHeight"] ? settings["pageHeight"] : "", pageOrientation: settings["pageOrientation"] ? settings["pageOrientation"] : ""});
        }
