import React, {Component} from 'react';
import {Modal, Select, Button, Icon, Tooltip } from 'antd';
import $ from 'jquery';

export default class Outlink extends Component{
		constructor(props) {
				super(props);
				this.owner = this.props.owner;
				this.state = {
					visible: true,
					
					targetFormId: '',
					targetFieldId: '',
					columns: [],
					linkages: []
				}
				if(props.using){
						this.state = {...this.state, ...this.owner.state[props.using]};
				}else{
						this.state = {...this.state, ...this.owner.state.defaultValue};	
				}
				if(this.state.targetFormId){
						this.changeTargetForm(this.state.targetFormId);			
				}
		}

		handleOk(){
				let state = {...this.state};
				delete state["visible"];
				delete state["columns"];
				this.props.updateOutlink(state);
				this.handleCancel();
		}

		handleCancel(){
				//this.setState({visible: false});
			if(this.props.closeOutLink){
					this.props.closeOutLink();
			}
			window.setTimeout(()=>{this.owner.setState({outLinkShowFlag: false, outLinkShowFlag2: false});}, 0);
		}
		
		
		changeTargetForm(value){
				let formId = this.props.formId;
				if(formId != ""){
						$.get('/general/appbuilder/web/appdesign/appform/items', {
									forms: value || '',
									formId: formId,
									with_id: true
								}, (res) => {
									this.setState({columns: res.data});
						});
						this.setState({targetFormId: value});
				}
		}

		render(){			
				let {formsOfApp, columnsOfForm, noTarget} = this.props;
				let forms = Object.keys(formsOfApp).map(key=> formsOfApp[key]);
				let columns = Object.keys(columnsOfForm).map(key=> columnsOfForm[key]);
				
				return (
						<div>
								<Modal title={noTarget? "设置链接表单" : "设置关联提取"} visible={this.state.visible} onOk={this.handleOk.bind(this)} onCancel={this.handleCancel.bind(this)} footer={[
										<Button key="back" type="ghost" size="large" onClick={this.handleCancel.bind(this)}>取 消</Button>,
          					<Button key="submit" type="primary" size="large" onClick={this.handleOk.bind(this)}>提 交</Button>
        				]}>
									<div className="ant-row ant-form-item">
				            <div className="ant-form-item-label">
				              <label>{noTarget? "链接表单" : "关联表单"}</label>
				              <span className="ant-input-wrapper config-typename">
													<Select value={[this.state.targetFormId]} onChange={this.changeTargetForm.bind(this)} style={{"width": 150}}>
															{
																	forms.map(form => <Select.Option value={form.id}><Tooltip title={form.name}>{form.name}</Tooltip></Select.Option>)
															}
													</Select>
				              </span>
				            </div>
				          </div>
				          {
				          	!noTarget ?
				          <div className="ant-row ant-form-item">
				            <div className="ant-form-item-label">
				              <label>字段显示为</label>
				              <span className="ant-input-wrapper config-typename">
													<Select value={[this.state.targetFieldId]} onChange={value => this.setState({targetFieldId: value})} style={{"width": 150}}>
															{
																	this.state.columns.map(column => <Select.Option value={column.id}><Tooltip title={column.name}>{column.name}</Tooltip></Select.Option>)
															}
													</Select>
				              </span>
				            </div>
				          </div> : null
				        	}
				          <div className="ant-row ant-form-item">
				          		<a href="javascript:" onClick={e => {
				          				let {linkages} = this.state;
				          				linkages.push({id: '', targetId: ''});
				          			this.setState({linkages});}}>{noTarget? "添加链接条件" : "添加关联条件"}</a><span> 格式为：本表单字段 = {noTarget? "链接表单字段" : "关联表单字段"}</span>
				          </div>
				          {
				          		this.state.linkages.map((linkage, index) =>  
				          				<div className="ant-row ant-form-item">
				          						<Select value={[linkage.id]} onChange={value=> {
				          								let {linkages} = this.state;
				          								linkages[index]['id'] = value;
				          								this.setState({linkages});
				          						}} style={{"width": 150}}>
				          								{
				          										columns.map(item => <Select.Option value={item.id}><Tooltip title={item.name}>{item.name}</Tooltip></Select.Option>)
				          								}
				          						</Select>
				          						<Select value={[linkage.targetId]} onChange={value=> {
				          								let {linkages} = this.state;
				          								linkages[index]['targetId'] = value;
				          								this.setState({linkages});
				          						}} style={{"width": 150, "margin-left": '10px'}}>
				          								{this.state.columns.map(item => <Select.Option value={item.id}><Tooltip title={item.name}>{item.name}</Tooltip></Select.Option>)}
				          						</Select>
				          						<a><Icon type="minus-circle" onClick={e => {
				          								let {linkages} = this.state;
				          								linkages = linkages.filter((linkage,index2)=>index2!==index);
				          							this.setState({linkages});}} style={{"cursor":"hand"}}/></a>
				          				</div>
				          		)
				          }
				         
        				</Modal>
						</div>
				);
		}
}