import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { Provider } from 'react-redux'
import AllReducers from './reducers/form';
import { createStore, applyMiddleware, combineReducers, compose } from 'redux'
import thunk from 'redux-thunk'
import {message} from 'antd';
import {SetFormId, fetchForm, fetchForms, fetchColumnsOfForm} from './actions/form';

const getQueryParams = () => {
    var qs = window.location.search.length > 0 ? window.location.search.substr(1) : '',
        args = {},
        items = qs.length > 0 ? qs.split('&') : [],
        item = null, name = null, value = null, i = 0, len = items.length;
    for (i = 0; i < len; i++) {
        item = items[i].split('=');
        name = decodeURIComponent(item[0]);
        value = decodeURIComponent(item[1]);

        if (name.length) {
            args[name] = value;
        }
    }
    return { ...args }
}

let urlParams = getQueryParams();
let {formId} = urlParams;

if (0 && formId == undefined) {
    message.error('未指定表单！');
}else{
		const store = createStore(combineReducers(AllReducers), {}, compose(
		    applyMiddleware(thunk),
		    window.devToolsExtension ? window.devToolsExtension() : f => f
		));

		store.dispatch(SetFormId(formId));
		store.dispatch(fetchForm());
		//store.dispatch(fetchForms());
		//store.dispatch(fetchColumnsOfForm(formId));
		//console.log(store.getState());

		ReactDOM.render(
		    <Provider store={store}>
		        <App />
		    </Provider>,
		    document.getElementById('root')
		)
}
