    merge = () => {
        let range = this.owner.owner.excel.getSelectedRangeLast()
        if (this.owner.owner.excel.getPlugin("mergeCells").mergedCellsCollection && this.owner.owner.excel.getPlugin("mergeCells").mergedCellsCollection.getByRange(range)) {
            this.owner.owner.excel.getPlugin("mergeCells").unmergeSelection(range);
        } else {
            this.owner.owner.excel.getPlugin("mergeCells").mergeSelection(range);
        }

        let mergeCells = []
        if(this.owner.owner.excel.getPlugin("mergeCells").mergedCellsCollection) {
            this.owner.owner.excel.getPlugin("mergeCells").mergedCellsCollection.mergedCells.map(mc => mergeCells.push({
                row: mc.row,
                col: mc.col,
                rowspan: mc.rowspan,
                colspan: mc.colspan
            }))
        }
        this.owner.owner.excel.updateSettings({mergeCells: mergeCells})
    }

    setAlign = (item) => {
        let sAlign = item.key
        this.owner.owner.excel.getSelectedRange().map((cellRange) => {
                let {from, to} = cellRange;
                this.fromTo(from, to);
                for (let row = from.row; row <= to.row; row++) {
                    for (let col = from.col; col <= to.col; col++) {
                        let meta = this.owner.owner.excel.getCellMeta(row, col);
                        if (!meta['className']) meta['className'] = "";
                        if (['htLeft', 'htCenter', 'htRight'].indexOf(sAlign) !== -1) {
                            meta['className'] = meta['className'].replace('htLeft', '')
                            meta['className'] = meta['className'].replace('htCenter', '')
                            meta['className'] = meta['className'].replace('htRight', '')
                        } else if (['htTop', 'htMiddle', 'htBottom'].indexOf(sAlign) !== -1) {
                            meta['className'] = meta['className'].replace('htTop', '')
                            meta['className'] = meta['className'].replace('htMiddle', '')
                            meta['className'] = meta['className'].replace('htBottom', '')
                        }
                        if (meta['className'].indexOf(sAlign) === -1) {
                            meta['className'] += ' ' + sAlign;
                        }
												if(!meta['def']){
														meta['def'] = {flag: true, styleName: {}}
												}else if(meta['def'] && !meta['def']['flag']){
		                        meta['def'] = {flag: true, styleName: {...meta['def']['styleName']}};
		                    }
                        switch(sAlign){
                        		case "htLeft":
                        				meta["def"]["styleName"]["text-align"] = "left";
                        				break;
                        		case "htCenter":
                        				meta["def"]["styleName"]["text-align"] = "center";
                        				break;
                        		case "htRight":
                        				meta["def"]["styleName"]["text-align"] = "right";
                        				break;
                        		case "htTop":
                        				meta["def"]["styleName"]["vertical-align"] = "top";
                        				break;
                        		case "htMiddle":
                        				meta["def"]["styleName"]["vertical-align"] = "middle";
                        				break;
                        		case "htBottom":
                        				meta["def"]["styleName"]["vertical-align"] = "bottom";
                        				break;
                        }
                        this.owner.owner.excel.setCellMetaObject(row, col, meta);
                    }
                }
            }
        )
        this.owner.owner.excel.render()
        if (['htLeft', 'htCenter', 'htRight'].indexOf(sAlign) !== -1) {
            this.owner.setState({align: sAlign})
        }else{
            this.owner.setState({verticalAlign: sAlign})
        }
    }

    setWordwrap = () => {
        this.owner.owner.excel.getSelectedRange().map((cellRange) => {
            let {from, to} = cellRange;
            this.fromTo(from, to);
            for (let row = from.row; row <= to.row; row++) {
                for (let col = from.col; col <= to.col; col++) {
                    let meta = this.owner.owner.excel.getCellMeta(row, col);
										if(!meta['def']){
												meta['def'] = {flag: true, styleName: {}}
										}else if(meta['def'] && !meta['def']['flag']){
                        meta['def'] = {flag: true, styleName: {...meta['def']['styleName']}};
                    }
                    meta["def"]["styleName"]['wordWrap'] = !meta["def"]["styleName"]['wordWrap']
                    this.owner.owner.excel.setCellMetaObject(row, col, meta);
                }
            }
        })
        this.owner.owner.excel.render()
    }

