import React from 'react';
import loadable from '@loadable/component'

export const LoadControlProps = (atype, formId, formsOfApp, columnsOfForm, owner, def, parent_list, codes) => {
		let formDetail = {codes};
		switch(atype){
				case 'text':
						const TextProps = loadable(() => import('./TextProps'), {});
					  return (<TextProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);
				case 'multi-text':
						const MultiTextProps = loadable(() => import('./MultiTextProps'), {});
					  return (<MultiTextProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);
				case 'number':
				  	const NumberProps = loadable(() => import('./NumberProps'), {});
					  return (<NumberProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);
				case 'currency':
				  	const CurrencyProps = loadable(() => import('./CurrencyProps'), {});
					  return (<CurrencyProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);				
				case 'date':
				  	const DateProps = loadable(() => import('./DateProps'), {});
					  return (<DateProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);				
				case 'radio':
				  	const RadioProps = loadable(() => import('./RadioProps'), {});
					  return (<RadioProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list} formDetail={formDetail}/>);						
				case 'checkbox':
				  	const CheckboxProps = loadable(() => import('./CheckboxProps'), {});
					  return (<CheckboxProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list} formDetail={formDetail}/>);					
				case 'select':
				  	const SelectProps = loadable(() => import('./SelectProps'), {});
					  return (<SelectProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list} formDetail={formDetail}/>);	
				  break;
				case 'address':
				  	const AddressProps = loadable(() => import('./AddressProps'), {});
					  return (<AddressProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'location':
				  	const LocationProps = loadable(() => import('./LocationProps'), {});
					  return (<LocationProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);	
				case 'attachment':
				  	const AttachmentProps = loadable(() => import('./AttachmentProps'), {});
					  return (<AttachmentProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'image':
				  	const ImageProps = loadable(() => import('./ImageProps'), {});
					  return (<ImageProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'dept-select':
				  	const DeptSelectProps = loadable(() => import('./DeptSelectProps'), {});
					  return (<DeptSelectProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'user-select':
				  	const UserSelectProps = loadable(() => import('./UserSelectProps'), {});
					  return (<UserSelectProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'autoNumber':
				  	const AutoNumberProps = loadable(() => import('./AutoNumberProps'), {});
					  return (<AutoNumberProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'barcode':
				  	const BarcodeProps = loadable(() => import('./BarcodeProps'), {});
					  return (<BarcodeProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'QRcode':
				  	const QRcodeProps = loadable(() => import('./QRcodeProps'), {});
					  return (<QRcodeProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'progressBar':
				  	const ProgressBarProps = loadable(() => import('./ProgressBarProps'), {});
					  return (<ProgressBarProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'link':
				  	const LinkProps = loadable(() => import('./LinkProps'), {});
					  return (<LinkProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'signature':
				  	const SignatureProps = loadable(() => import('./SignatureProps'), {});
					  return (<SignatureProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);					
				case 'button':
				  	const ButtonProps = loadable(() => import('./ButtonProps'), {});
					  return (<ButtonProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);	
				case 'list':
				  	const ListProps = loadable(() => import('./ListProps'), {});
					  return (<ListProps formId={formId} formsOfApp={formsOfApp} columnsOfForm={columnsOfForm} owner={owner} def={def} parent_list={parent_list}/>);								  					  
		}
		return null;
}