import React, { Component } from 'react';
import {Menu, Button, message, Icon} from 'antd';
import $ from 'jquery';
import {allControlles, controlIndexes} from '../../Controlles';

export let dataStyle = {
    "all": {},
    "cells": {},
    "class": {}
};

let sheetIndex = 0;

export let max_row_with_style = 1, max_col_with_style = 1; 

export function setActiveSheetIndex(iSheetIndex){
    sheetIndex = iSheetIndex;
}

function htmlspecialchars_decode(str){
    if(typeof (str) == "string") {
        str = str.replace(/&amp;/g, '&');
        str = str.replace(/&lt;/g, '<');
        str = str.replace(/&gt;/g, '>');
        str = str.replace(/&quot;/g, "''");
        str = str.replace(/&#039;/g, "'");
    }
    return str;
}

export function Handson_renderer(instance, td, row, col, prop, value, cellProperties) {
		if(row === 0 && col === 0){
				max_row_with_style = max_col_with_style = 1;
		}
    var classconf = dataStyle.class[sheetIndex + "_" + row + "_" + col];//获取指定格子的样式
    if(classconf) {
        $(td).addClass(classconf);
    }

    if($.isEmptyObject(dataStyle.cells) && $.isEmptyObject(dataStyle.class)) {
        if(cellProperties["def"] && cellProperties["def"]["styleName"]){     	
            $.each(cellProperties["def"]["styleName"], function (i, n){
                if(1 || i != "background-color") {
                    if(n != td.style[i]){
                    		//$(td).css(i, n)
                    		td.style[i] = n;
                  	}
                }
            });
            //delete cellProperties["def"]["styleName"];
        }
    }

//    if(cellProperties["style"]){
//        $.each(cellProperties["style"], function (i, n){
//              if(n != td.style[i]){
//              		//$(td).css(i, n)
//              		td.style[i] = n;
//            	}
//        });
//    }
    if(cellProperties["className"]){
        $(td).addClass(cellProperties["className"]);
    }

		let save_style = 1;//instance.getSettings()["saveStyle"];
		if(save_style){
		    if(cellProperties.def == undefined){
		        cellProperties["def"] = {'flag': true, 'styleName': {}}
		    }
		    //字体
		    if(1 || cellProperties["def"]["styleName"]["font-family"] === undefined){
				    cellProperties["def"]["styleName"]["font-family"] = $(td).css("font-family")
				    cellProperties["def"]["styleName"]["font-size"] = $(td).css("font-size")
				    cellProperties["def"]["styleName"]["font-weight"] = $(td).css("font-weight")
				    cellProperties["def"]["styleName"]["font-style"] = $(td).css("font-style")
				    cellProperties["def"]["styleName"]["text-decoration"] = $(td).css("text-decoration")
				    cellProperties["def"]["styleName"]["color"] = $(td).css("color")
		  	}
		    //边框
		    if(1 || cellProperties["def"]["styleName"]["border-width"] === undefined){
				    cellProperties["def"]["styleName"]["border-width"] = $(td).css("border-width")
				    cellProperties["def"]["styleName"]["border-style"] = $(td).css("border-style")
				    cellProperties["def"]["styleName"]["border-color"] = $(td).css("border-color")		    

				    cellProperties["def"]["styleName"]["border-left-width"] = $(td).css("border-left-width")
				    cellProperties["def"]["styleName"]["border-left-style"] = $(td).css("border-left-style")
				    cellProperties["def"]["styleName"]["border-left-color"] = $(td).css("border-left-color")
						if(cellProperties["def"]["styleName"]["border-left-style"] === "solid" && cellProperties["def"]["styleName"]["border-left-width"] === "1px" && cellProperties["def"]["styleName"]["border-left-color"] === "rgb(204, 204, 204)"){
				    		delete cellProperties["def"]["styleName"]["border-left-width"];
				    		delete cellProperties["def"]["styleName"]["border-left-style"];
				    		delete cellProperties["def"]["styleName"]["border-left-color"];
				    }
				    
				    cellProperties["def"]["styleName"]["border-right-width"] = $(td).css("border-right-width")
				    cellProperties["def"]["styleName"]["border-right-style"] = $(td).css("border-right-style")
				    cellProperties["def"]["styleName"]["border-right-color"] = $(td).css("border-right-color")
						if(cellProperties["def"]["styleName"]["border-right-style"] === "solid" && cellProperties["def"]["styleName"]["border-right-width"] === "1px" && cellProperties["def"]["styleName"]["border-right-color"] === "rgb(204, 204, 204)"){
				    		delete cellProperties["def"]["styleName"]["border-right-width"];
				    		delete cellProperties["def"]["styleName"]["border-right-style"];
				    		delete cellProperties["def"]["styleName"]["border-right-color"];
				    }

				    cellProperties["def"]["styleName"]["border-top-width"] = $(td).css("border-top-width")
				    cellProperties["def"]["styleName"]["border-top-style"] = $(td).css("border-top-style")
				    cellProperties["def"]["styleName"]["border-top-color"] = $(td).css("border-top-color")
						if(cellProperties["def"]["styleName"]["border-top-style"] === "solid" && cellProperties["def"]["styleName"]["border-top-width"] === "1px" && cellProperties["def"]["styleName"]["border-top-color"] === "rgb(204, 204, 204)"){
				    		delete cellProperties["def"]["styleName"]["border-top-width"];
				    		delete cellProperties["def"]["styleName"]["border-top-style"];
				    		delete cellProperties["def"]["styleName"]["border-top-color"];
				    }
				    
				    cellProperties["def"]["styleName"]["border-bottom-width"] = $(td).css("border-bottom-width")
				    cellProperties["def"]["styleName"]["border-bottom-style"] = $(td).css("border-bottom-style")
				    cellProperties["def"]["styleName"]["border-bottom-color"] = $(td).css("border-bottom-color")
						if(cellProperties["def"]["styleName"]["border-bottom-style"] === "solid" && cellProperties["def"]["styleName"]["border-bottom-width"] === "1px" && cellProperties["def"]["styleName"]["border-bottom-color"] === "rgb(204, 204, 204)"){
				    		delete cellProperties["def"]["styleName"]["border-bottom-width"];
				    		delete cellProperties["def"]["styleName"]["border-bottom-style"];
				    		delete cellProperties["def"]["styleName"]["border-bottom-color"];
				    }
				    
				    if(cellProperties["def"]["styleName"]["border-right-width"] || cellProperties["def"]["styleName"]["border-bottom-width"]){
				    		if(max_row_with_style < row + 1){
				    				max_row_with_style = row + 1;
				    		}
				    		if(max_col_with_style < col + 1){
				    				max_col_with_style = col + 1;
				    		}
				    }				
				}
		    //背景
		    if(1 || cellProperties["def"]["styleName"]["background-color"] === undefined){
		    		cellProperties["def"]["styleName"]["background-color"] = $(td).css("background-color")
		  	}
		    //对齐
		    if(1 || cellProperties["def"]["styleName"]["text-align"] === undefined){
				    cellProperties["def"]["styleName"]["text-align"] = $(td).css("text-align")
				    cellProperties["def"]["styleName"]["vertical-align"] = $(td).css("vertical-align")
		  	}
  	}

		if(cellProperties["cellDef"] && cellProperties['cellDef']['type']){
				if(!cellProperties["readOnly"]){
						cellProperties["readOnly"] = true;
				}
				let theControl = allControlles[controlIndexes[cellProperties['cellDef']['type']]];
				td.innerHTML = '<i aria-label="icon: undefined" class="anticon" style="color: green; font-size: 20px;" row=' + row + ' col=' + col +'>' + theControl.icon2 + '</i>';
		} else if(cellProperties["def"] && cellProperties["def"]["image"]){
				td.innerHTML = '<img src="' + cellProperties["def"]["image"] + '"/>';
		}else{
				if(!value) value = '';
    		td.innerHTML = htmlspecialchars_decode(value);
    }
    return td;
}

//屏幕像素（像素/英寸）
function getWindowDPI() {
    var arrDPI = new Array;
    if (window.screen.deviceXDPI) {
        arrDPI[0] = window.screen.deviceXDPI;
        arrDPI[1] = window.screen.deviceYDPI;
    }
    else {
        var tmpNode = document.createElement("DIV");
        tmpNode.style.cssText = "width:1in;height:1in;position:absolute;left:0px;top:0px;z-index:99;visibility:hidden";
        document.body.appendChild(tmpNode);
        arrDPI[0] = parseInt(tmpNode.offsetWidth);
        arrDPI[1] = parseInt(tmpNode.offsetHeight);
        tmpNode.parentNode.removeChild(tmpNode);
    }
    return arrDPI;
}

//像素转为厘米长度单位
export function pixelToCM(i_len) {
    let dpi = getWindowDPI();
    let f_in = i_len / dpi
    return Math.floor(f_in * 2.54)
}

//厘米长度单位转为像素
export function cmToPixel(i_len){
    let dpi = getWindowDPI();
    let f_in = i_len * 0.39370078740157
    return Math.floor(f_in * dpi[0])
}

