import React, { Component } from 'react';
import { Modal, InputNumber, Select, Row, Col, Button, Input} from 'antd';
const Option = Select.Option;

class ChartOptions extends Component {
		constructor(props){
	      super(props);
	      
	      this.state = {
	      		visible: false,
	      		chartOptions: {}	
	      }
	      this.owner = this.props.owner;
	  }
	  
	  componentWillReceiveProps(nextProps) {
	  		let {visible, chartOptions} = nextProps;
	  		this.setState({visible, chartOptions});
	  }
	  
	  changeOption(index, e){
	  		let {xOptions} = this.state;
	  		xOptions[index] = e.target.value;
	  		this.setState({xOptions});
	  }
	  
	  handleOk(){
	  		this.owner.setState({chartOptions: this.state.chartOptions, chartOptionsVisible: false});
	  }
	  
	  handleCancel(){
	  		this.owner.setState({chartOptionsVisible: false});
	  }
	  
	  setMinValue(e){
	  		let {chartOptions} = this.state;
	  		chartOptions.minValue = e.target.value;
	  		this.setState({chartOptions});
	  }
	  
	  setMaxValue(e){
	  		let {chartOptions} = this.state;
	  		chartOptions.maxValue = e.target.value;
	  		this.setState({chartOptions});
	  }
	  
	  setTrendMethod(value){
	  		let {chartOptions} = this.state;
	  		chartOptions.trendMethod = value;
	  		this.setState({chartOptions});	  		
	  }
	  
	  setTrendType(value){
	  		let {chartOptions} = this.state;
	  		chartOptions.trendType = value;
	  		this.setState({chartOptions});	  		
	  }	
	  
	  setTrendName(e){
	  		let {chartOptions} = this.state;
	  		chartOptions.trendName = e.target.value;
	  		this.setState({chartOptions});	  		
	  }		    

	  setTrendDecimal(value){
	  		let {chartOptions} = this.state;
	  		chartOptions.trendDecimal = value;
	  		this.setState({chartOptions});	  		
	  }	
	  	  
	  render() {  		
		    let {chartOptions} = this.state;
		    
		    return (
		      <div>
		        <Modal title="图表选项" visible={this.state.visible} onOk={this.handleOk.bind(this)} onCancel={this.handleCancel.bind(this)}
						footer={[
		            <Button key="back" type="ghost" onClick={this.handleCancel.bind(this)}>返 回</Button>,
		            <Button key="submit" type="primary" onClick={this.handleOk.bind(this)}>
		              确 定
		            </Button>,
	          ]} width="600px"		        
		        >
        				<Row gutter={16} style={{margin: '5px'}}>
        						<Col span={5}><label>最 小 值：<InputNumber value={chartOptions.minValue} onChange={this.setMinValue.bind(this)}/></label></Col>
        						<Col span={5}><label>最 大 值：<InputNumber value={chartOptions.maxValue} onChange={this.setMaxValue.bind(this)}/></label></Col>
								</Row>
								<Row gutter={16} style={{margin: '5px'}}>
										<Col span={24}><label>趋势分析：
													<Select value={chartOptions.trendMethod} onChange={this.setTrendMethod.bind(this)} style={{width:'120px'}}>
															<Option value="" key="">无</Option>
															<Option value="avg" key="avg">平均值</Option>
													</Select>
											    <Select value={chartOptions.trendType} onChange={this.setTrendType.bind(this)} style={{width:'120px'}}>
	      											<Option value="" key=""></Option>
											      	<Option value="column" key="column">柱状图</Option>
											      	<Option value="stackedColumn" key="stackedColumn">栈柱状图</Option>
											      	<Option value="bar" key="bar">条状图</Option>
											      	<Option value="stackedBar" key="stackedBar">栈条状图</Option>
											      	<Option value="line" key="line">折线图</Option>
											      	<Option value="pie" key="pie">饼状图</Option>
											      	<Option value="area" key="area">面积图</Option>
											      	<Option value="stackedArea" key="stackedArea">栈面积图</Option>
											      	<Option value="funnel" key="funnel">烟囱图</Option>
											      	<Option value="radar" key="radar">雷达图</Option>
											      	<Option value="gauge" key="gauge">仪表盘</Option>
											    </Select>
											    <Input value={chartOptions.trendName} onChange={this.setTrendName.bind(this)} placeholder='名称' style={{width: 100}} title='名称'/>
											    <InputNumber min={0} max={6} value={chartOptions.trendDecimal} onChange={this.setTrendDecimal.bind(this)} placeholder='小数位' style={{width: 70}} title='保留小数位数'/>
										</label></Col>
								</Row>
		        </Modal>
	        </div>
	      )
		}	  
}

export default ChartOptions;