import React, { Component } from 'react';
import Main from './Main';
import './App.css';
import { connect } from 'react-redux';
import {setFormDetailFlowId, setFormDetailIsStop, saveForm, setFormDetailData} from './actions/form';

const mapStateToProps = (state, ownProps) => {
		return {
				formId: state.formId,
				formDetail: state.formDetail,
				formsOfApp : state.formsOfApp,
				columnsOfForm: state.columnsOfForm
		}
}

const mapDispatchToProps = (dispatch) => {
	return {
			setFlowId: (flowId) => {
					dispatch(setFormDetailFlowId(flowId))
			},
			setIsStop: (isStop) => {
					dispatch(setFormDetailIsStop(isStop))
			},
			saveForm: (formId, isStop, Workbook, callback) => {
					dispatch(saveForm(formId, isStop, Workbook, callback));
			},
			setFormDetailData: (form) => {
					dispatch(setFormDetailData(form));
			}
	}
}

class App extends Component {
  render() {
    return (
				<Main {...this.props}/>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);