const ButtonGroup = Button.Group

class ReportOption extends Component {
    constructor(props){
        super(props);
        this.owner = this.props.owner;
        this.state = {
            displayColorPicker: false,
            color: 'black',
            displayBgColorPicker: false,
            bgColor: 'white',
            displayFrameColorPicker: false,
            frameColor: 'black',
            frameSize: 1,
            frameStyle: 'solid',

            expandType: '',//当前扩展方式
            leftParentType: '1',//左父格
            leftParentCell: '',
            topParentType: '1',//上父格
            topParentCell: '',
            fontName: '',//字体名称
            fontSize: 12,
            fontColor: 'black',
            bold: false,
            italic: false,
            underLine: false,
            lineThrough: false,
            frameType: 6,//边框
            merge: false,//合并标志
            align: 'htLeft',
            verticalAlign: 'htMiddle',
            wordWrap: false,
            dataType: '',
            thousandth: false,
            fractionLen: 0,
            currencySign: '￥',
            displayStyle: '',

            pageType: '',
            pageWidth: '',
            pageHeight: '',
            pageOrientation: '1',

            parentCellShowFlag: false,
            frameFormatShowFlag: false,
            alignShowFlag: false,
            verticalAlignShowFlag: false,
            pageShowFlag: false,
            insertShowFlag: false,
            showChartPropertyFlag: false,
            dataTypePropertyFlag: false
        }
        this.barAction = new BarAction(this);
        window.barAction = this.barAction;
    }

    componentWillReceiveProps(nextProps){
       let {color, bgColor, expandType, leftParentType, leftParentCell, topParentType, topParentCell, fontName, fontSize, fontColor, bold, italic, underLine, lineThrough, merge, align, verticalAlign, wordWrap, dataType, thousandth, fractionLen, currencySign, displayStyle} = nextProps.def;
       let {pageType, pageWidth, pageHeight, pageOrientation, activeChart} = nextProps
       this.setState({color, bgColor, expandType, leftParentType, leftParentCell, topParentType, topParentCell, fontName, fontSize, fontColor, bold, italic, underLine, lineThrough, merge, align, verticalAlign, wordWrap, dataType, thousandth, fractionLen, currencySign, displayStyle,pageType, pageWidth, pageHeight, pageOrientation, showChartPropertyFlag: activeChart? true : this.state.showChartPropertyFlag})
    }

    handleColorClick = () => {
        this.setState({ displayColorPicker: !this.state.displayColorPicker })
    }

    handleColorClose = () => {
        this.setState({ displayColorPicker: false })
    }

    handleColorChange = (color) => {
        this.setState({ color: color.hex })
        this.barAction.setColor(color.hex)
    }

    handleBgColorClick = () => {
        this.setState({ displayBgColorPicker: !this.state.displayBgColorPicker })
    }

    handleBgColorClose = () => {
        this.setState({ displayBgColorPicker: false })
    }

    handleBgColorChange = (color) => {
    		this.setState({ bgColor: color.hex })
        this.barAction.setBgColor(color.hex)
    }

    handleFrameColorClick = () => {
        this.setState({ displayFrameColorPicker: !this.state.displayFrameColorPicker })
    }

    handleFrameColorClose = () => {
        this.setState({ displayFrameColorPicker: false })
    }

    handleFrameColorChange = (color) => {
        this.setState({ frameColor: color.hex })
    }

    setframeSize = (iWidth) => {
        this.setState({frameSize: iWidth})
    }

    render(){
        const font_menu = (
            <Menu onClick={this.barAction.setFont} selectedKeys={[this.state.fontName]}>
                {
                    fontFamilyList.map(font => <Menu.Item key={font.en}>{font.ch}</Menu.Item>)
                }
            </Menu>
        )
        const size_menu = (
            <Menu onClick={this.barAction.setFontSize} selectedKeys={[this.state.fontSize]}>
                {
                    fontFamilySize.map(size => <Menu.Item key={size}>{size}</Menu.Item>)
                }
            </Menu>
        )
        const popover = {
            position: 'absolute',
            zIndex: '200',
            left: '111'
        }
        const popover2 = {
            position: 'absolute',
            zIndex: '200',
            left: '0'
        }
        const popover3 = {
            position: 'absolute',
            zIndex: '200'
        }
        const cover = {
            position: 'fixed',
            top: '0px',
            right: '0px',
            bottom: '0px',
            left: '0px',
        }
        const frame_format = (
            <div style={{'backgroundColor': '#f3f3f3'}}>
                <Button title="全部" onClick={this.barAction.setFrame.bind(this, 1, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 1 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={allBorderSvg} style={{ color: 'green'}}/></Button>
                <Button title="外边框" onClick={this.barAction.setFrame.bind(this, 2, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 2 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={outerBorderSvg} style={{ color: 'green'}}/></Button>
                <Button title="内边框" onClick={this.barAction.setFrame.bind(this, 3, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 3 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={innerBorderSvg} style={{ color: 'green'}}/></Button>
                <Button title="水平边框" onClick={this.barAction.setFrame.bind(this, 4, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 4 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={horizontalBorderSvg} style={{ color: 'green'}}/></Button>
                <Button title="垂直边框" onClick={this.barAction.setFrame.bind(this, 5, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 5 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={verticalBorderSvg} style={{ color: 'green'}}/></Button>
                <br/>
                <Button title="无边框" onClick={this.barAction.setFrame.bind(this, 6, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={0 && this.state.frameType === 6 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={clearBorderSvg} style={{ color: 'green'}}/></Button>
                <Button title="左外边框" onClick={this.barAction.setFrame.bind(this, 7, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 7 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={leftBorderSvg} style={{ color: 'green'}}/></Button>
                <Button title="上外边框" onClick={this.barAction.setFrame.bind(this, 8, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 8 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={topBorderSvg} style={{ color: 'green'}}/></Button>
                <Button title="右外边框" onClick={this.barAction.setFrame.bind(this, 9, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 9 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={rightBorderSvg} style={{ color: 'green'}}/></Button>
                <Button title="下外边框" onClick={this.barAction.setFrame.bind(this, 10, this.state.frameColor, this.state.frameSize, this.state.frameStyle)} type={this.state.frameType === 10 ? "primary" : "default"} style={{"width": "80px"}}><Icon component={bottomBorderSvg} style={{ color: 'green'}}/></Button>
                <hr/>
                <Radio.Group value={this.state.frameStyle} onChange = {(e) => this.setState({frameStyle: e.target.value})}><Radio.Button key="none" value="none" style={{"width": "80px"}}></Radio.Button><Radio.Button key="solid" value="solid" style={{"width": "80px"}}><Icon component={lineSvg}/></Radio.Button><Radio.Button key="dotted" value="dotted" style={{"width": "80px"}}><Icon type="small-dash" style={{ color: 'green'}}/></Radio.Button><Radio.Button key="dashed" value="dashed" style={{"width": "80px"}}><Icon type="dash" style={{ color: 'green'}}/></Radio.Button><Radio.Button key="double" value="double" style={{"width": "80px", color: 'green'}}>=</Radio.Button></Radio.Group>
                <hr/>
                <Button title="填充颜色"  onClick={ this.handleFrameColorClick } style={{"backgroundColor": this.state.frameColor,"width": "400px"}}>边框颜色</Button>
                { this.state.displayFrameColorPicker ? <div style={ popover3 }>
                    <div style={ cover } onClick={ this.handleFrameColorClose }/>
                    <ChromePicker color={ this.state.frameColor } onChange={ this.handleFrameColorChange }/>
                </div> : null }
                <hr/>
                <Slider defaultValue={1} min={1} max={20} onChange={this.setframeSize} value={this.state.frameSize}/>
            </div>
        )
        const align_type = (
            <Menu onClick={this.barAction.setAlign.bind(this)} selectedKeys={[this.state.align]}>
                <Menu.Item key='htLeft'><Icon type="align-left" style={{ color: 'green'}}/></Menu.Item>
                <Menu.Item key='htCenter'><Icon type="align-center" style={{ color: 'green'}}/></Menu.Item>
                <Menu.Item key='htRight'><Icon type="align-right" style={{ color: 'green'}}/></Menu.Item>
            </Menu>
        )
        const vertical_type = (
            <Menu onClick={this.barAction.setAlign.bind(this)} selectedKeys={[this.state.verticalAlign]}>
                <Menu.Item key='htTop'><Icon component={alignTopSvg} style={{ color: 'green'}}/></Menu.Item>
                <Menu.Item key='htMiddle'><Icon component={alignMiddleSvg} style={{ color: 'green'}}/></Menu.Item>
                <Menu.Item key='htBottom'><Icon component={alignBottomSvg} style={{ color: 'green'}}/></Menu.Item>
            </Menu>
        )

        const expand_type = (
            <Menu selectedKeys={[this.state.expandType]} onClick={this.barAction.setExpandType.bind(this)}>
                <Menu.Item key="0">纵向扩展（按行向下）</Menu.Item>
                <Menu.Item key="1">横向扩展（按列向右）</Menu.Item>
                <Menu.Item key="2">不扩展</Menu.Item>
            </Menu>
        )

        const parent_cell = (
            <div style={{'backgroundColor': '#f3f3f3'}}>
                <div><label>左父格：<Radio.Group value={this.state.leftParentType} onChange={this.barAction.setParentType.bind(this, 'leftParentType')}><Radio.Button key="0" value="0">无</Radio.Button><Radio.Button key="1" value="1">默认</Radio.Button><Radio.Button key="2" value="2">自定义</Radio.Button></Radio.Group>
                    <Tooltip title="请输入有效的单个单元格，例如：A1，B2等"><Input style={{"width": "40px"}} placeholder="A2" disabled={this.state.leftParentType=="2" ? false : true} value={this.state.leftParentCell} onChange={this.barAction.setParentCell.bind(this, 'leftParentCell')}/></Tooltip></label>
                </div>

                <div>
                    <label>上父格：<Radio.Group value={this.state.topParentType}  onChange={this.barAction.setParentType.bind(this, 'topParentType')}><Radio.Button key="0" value="0">无</Radio.Button><Radio.Button key="1" value="1">默认</Radio.Button><Radio.Button key="2" value="2">自定义</Radio.Button></Radio.Group>
                        <Tooltip title="请输入有效的单个单元格，例如：A1，B2等"><Input style={{"width": "40px"}} placeholder="B1" disabled={this.state.topParentType=="2" ? false : true} value={this.state.topParentCell} onChange={this.barAction.setParentCell.bind(this, 'topParentCell')}/></Tooltip></label>
                </div>
            </div>
        )

        const page_options = (
            <div style={{'backgroundColor': '#f3f3f3'}}>
                <div>
                <Radio.Group value={this.state.pageType} onChange={this.barAction.setPageType.bind(this)}><Radio.Button key="" value="">无</Radio.Button><Radio.Button key="1" value="1">A3</Radio.Button><Radio.Button key="2" value="2">A4</Radio.Button><Radio.Button key="3" value="3">A5</Radio.Button><Radio.Button key="4" value="4">B4</Radio.Button><Radio.Button key="5" value="5">B5</Radio.Button><Radio.Button key="25" value="25">自定义</Radio.Button></Radio.Group>
                    宽度：<Input style={{"width": "40px"}} disabled={this.state.pageType === "25" ? false : true} value={this.state.pageWidth} onChange={this.barAction.setPageWidth.bind(this)}/>厘米
                    高度：<Input style={{"width": "40px"}} disabled={this.state.pageType === "25" ? false : true} value={this.state.pageHeight} onChange={this.barAction.setPageHeight.bind(this)}/>厘米
                </div>
                <div>
                    <Radio.Group value={this.state.pageOrientation} onChange={this.barAction.setPageOrientation.bind(this)}><Radio.Button key="1" value="1" style={{'width': '280px'}}>纵向</Radio.Button><Radio.Button key="2" value="2" style={{'width': '280px'}}>横向</Radio.Button></Radio.Group>
                </div>
            </div>
        )

        const insert_menu = (
        		<Menu onClick={this.barAction.insertObject.bind(this)}>
        				{/*<Menu.SubMenu title='图片'>
        						<Menu.Item key="inject_pic">
        								<Upload accept={"image/*"} action={"/general/appbuilder/web/appdesign/appupload/upload"} showUploadList={false} data={{fileId: "file"}} onChange={this.insertPictrue.bind(this, 1)}>
        								嵌入单元格中</Upload>
        						</Menu.Item>
        						<Menu.Item key="float_pic">
        								<Upload accept={"image/*"} action={"/general/appbuilder/web/appdesign/appupload/upload"} showUploadList={false} data={{fileId: "file"}} onChange={this.insertPictrue.bind(this, 2)}>
        								浮在单元格上</Upload>
        						</Menu.Item>
        				</Menu.SubMenu>*/}
        				<Menu.Item key='insert_pic'><Upload accept={"image/*"} action={"/general/appbuilder/web/appdesign/appupload/upload"} showUploadList={false} data={{fileId: "file"}} onChange={this.insertPictrue.bind(this, 2)}><Icon type="picture" style={{ color: 'green'}}/></Upload></Menu.Item>
        				<Menu.Item key='insert_chart'><Icon type="pie-chart" style={{ color: 'green'}}/></Menu.Item>
        		</Menu>
        );

        return(
            <div className="rep-filter">
                <div className="u-option-btn-wrapper">
                		{
                			this.owner.props.setForm !== undefined ?
                    <ButtonGroup>
                        <Button icon="download" title="导出表样" onClick={this.barAction.exportExcel.bind(this)} style={{ color: 'green'}}></Button>
                    </ButtonGroup> : null
                  	}
                  	{
                			this.owner.props.setForm !== undefined ?
                    <ButtonGroup>
                        <Upload accept={"application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"} action={"/general/appbuilder/web/formCenter/excel/import"} showUploadList={false} data={{fileId: "file"}} onChange={this.importExcel.bind(this)}>
                        <Button icon="upload" title="导入表样" style={{ color: 'green'}}></Button>
                        </Upload>
                    </ButtonGroup> : null
                  	}
                    {/*<ButtonGroup>
                        <Button icon="appstore-o" title="数据设置" onClick={this.barAction.setFetchData}></Button>
                        <Dropdown overlay={expand_type}>
                        <Button icon="share-alt" title="扩展设置"></Button>
                        </Dropdown>
                        <Dropdown overlay={parent_cell} visible={this.state.parentCellShowFlag} onVisibleChange={(flag) => this.setState({ parentCellShowFlag: flag })}>
                        <Button icon="block" title="父子格"></Button>
                        </Dropdown>
                    </ButtonGroup>*/}
                    <ButtonGroup>
                        <Button icon="copy" title="复制" onClick={this.barAction.copy} style={{ color: 'green'}}></Button>
                        <Button icon="scissor" title="剪切" onClick={this.barAction.cut} style={{ color: 'green'}}></Button>
                        <Button icon="snippets" title="粘贴" onClick={this.barAction.paste} style={{ color: 'green'}}></Button>
                    </ButtonGroup>
                    <ButtonGroup>
                        <Dropdown overlay={font_menu}>
                            <Button title="字体">
                                {/*fontEnToCn(this.state.fontName != undefined && this.state.fontName != "" && this.state.fontName.indexOf(',') === -1 ? this.state.fontName : "A")*/}
                                <Icon component={fontSvg} style={{ color: 'green'}}/>
                                <Icon type="down"  style={{ color: 'green'}}/>
                            </Button>
                        </Dropdown>
                        <Dropdown overlay={size_menu}>
                            <Button icon="font-size" title="字体大小" style={{ color: 'green'}}>
                                {/*parseInt(this.state.fontSize) > 0 ? this.state.fontSize : 12*/}<Icon type="down" style={{ color: 'green'}}/>
                            </Button>
                        </Dropdown>
                        <Button icon="font-colors" title="字体颜色" onClick={ this.handleColorClick } style={{"color": this.state.color}} style={{ color: 'green'}}></Button>
                        { this.state.displayColorPicker ? <div style={ popover }>
                            <div style={ cover } onClick={ this.handleColorClose }/>
                            <ChromePicker color={ this.state.color } onChange={ this.handleColorChange }/>
                        </div> : null }
                        <Button icon="bold" title="粗体" onClick={this.barAction.setBold} type={this.state.bold ? "primary" : "default"} style={{ color: 'green'}}></Button>
                        <Button icon="italic" title="斜体" onClick={this.barAction.setItalic} type={this.state.italic ? "primary" : "default"} style={{ color: 'green'}}></Button>
                        <Button icon="underline" title="下划线" onClick={this.barAction.setUnderline} type={this.state.underLine ? "primary" : "default"} style={{ color: 'green'}}></Button>
                        <Button icon="strikethrough" title="删除线" onClick={this.barAction.setLineThrough} type={this.state.lineThrough ? "primary" : "default"} style={{ color: 'green'}}></Button>
                    </ButtonGroup>
                    <ButtonGroup>
                        <Button icon="bg-colors" title="填充颜色"  onClick={ this.handleBgColorClick } style={{"backgroundColor": this.state.bgColor}} style={{ color: 'green'}}></Button>
                        { this.state.displayBgColorPicker ? <div style={ popover2 }>
                            <div style={ cover } onClick={ this.handleBgColorClose }/>
                            <ChromePicker color={ this.state.bgColor } onChange={ this.handleBgColorChange }/>
                        </div> : null }
                        <Dropdown overlay={frame_format} visible={this.state.frameFormatShowFlag} onVisibleChange={(flag) => this.setState({ frameFormatShowFlag: flag })}>
                            <Button icon="table" title="边框" style={{ color: 'green'}}><Icon type="down" style={{ color: 'green'}}/>
                            </Button>
                        </Dropdown>
                        <Button title="合并或取消合并单元格" onClick = {this.barAction.merge}><Icon component={mergeSvg} style={{ color: 'green'}}/></Button>
                    </ButtonGroup>
                    <ButtonGroup>
                        <Dropdown overlay={align_type} visible={this.state.alignShowFlag} onVisibleChange={(flag) => this.setState({ alignShowFlag: flag })}>
                            <Button icon={this.state.align === "htLeft" ? "align-left" : (this.state.align === "htRight" ? "align-right" : (this.state.align === "htCenter" ? "align-center" : "align-center"))} title="水平对齐" style={{ color: 'green'}}>
                                 <Icon type="down" theme="twoTone" twoToneColor="#52c41a" style={{ color: 'green'}}/>
                            </Button>
                        </Dropdown>
                        <Dropdown overlay={vertical_type} visible={this.state.verticalAlignShowFlag} onVisibleChange={(flag) => this.setState({ verticalAlignShowFlag: flag })}>
                            <Button title="垂直对齐">
                                <Icon component={this.state.verticalAlign === "htTop" ? alignTopSvg : (this.state.verticalAlign === "htBottom" ? alignBottomSvg : (this.state.verticalAlign === "htMiddle" ? alignMiddleSvg : alignMiddleSvg))} style={{ color: 'green'}}/><Icon type="down" style={{ color: 'green'}}/>
                            </Button>
                        </Dropdown>
                        <Button icon="enter" title="自动换行" onClick={this.barAction.setWordwrap} style={{ color: 'green'}}></Button>
                    </ButtonGroup>
                    {
                			this.owner.props.setForm !== undefined ?
                    <ButtonGroup>
                        <Dropdown overlay={insert_menu} visible={this.state.insertShowFlag} onVisibleChange={(flag) => this.setState({ insertShowFlag: flag })}>
                        		<Button title='插入图片或图表' icon='picture' style={{ color: 'green'}}></Button>
                        </Dropdown>
                        <Dropdown overlay={page_options} visible={this.state.pageShowFlag} onVisibleChange={(flag) => this.setState({ pageShowFlag: flag })}>
                            <Button title="页面设置">
                                <Icon component={pageSvg} style={{ color: 'green'}}/><Icon type="down" style={{ color: 'green'}}/>
                            </Button>
                        </Dropdown>                    		
                    </ButtonGroup> : null
                  	}
                </div>
                <ChartProperty visible={this.state.showChartPropertyFlag} owner={this} activeChart={this.props.activeChart} items={this.props.items}></ChartProperty>
                <DataTypeProperty modalFlag={this.state.dataTypePropertyFlag} 
                    setModalFlag={()=>{
                        this.setState({dataTypePropertyFlag: false});
                    }} 
                    {...this.props} owner={this}></DataTypeProperty>                
			</div>
        )
    }

    componentDidMount() {
        $("div.ant-upload-list").hide();
    }

    importExcel(info){
        if (info.file.status !== 'uploading') {
        }
        if (info.file.status === 'done') {
            this.doLoadExcel(info.file.response)
        } else if (info.file.status === 'error') {
            message.error(`${info.file.name} 导入失败！`);
        }
    }

    doLoadExcel(excelFile) {
        //console.log(excel);
        if (typeof excelFile === "string" && excelFile === "") return;

        // dataStyle["class"] = excel["cellClass"];
        $("style[myexcel]").remove();
        var D = document, cell_style = D.createElement('style');
        cell_style.setAttribute("type", "text/css");
        cell_style.setAttribute("myexcel", "myexcel");
        cell_style.styleSheet && (cell_style.styleSheet.cssText = excelFile.css) || cell_style.appendChild(D.createTextNode(excelFile.css));
        D.getElementsByTagName('head')[0].appendChild(cell_style);

        let sheets = [];
        excelFile.sheets.map((sheet, index) =>
            sheets.push({index: '' + (index + 1), name: sheet.name, cellClass: sheet.data.cellClass, cellSettings: {colWidths: sheet.data.colWidths, rowHeights: sheet.data.rowHeights, mergeCells: sheet.data.mergeCells, datas: sheet.data.cellValue, contentCols: parseInt(sheet.data.maxColumn) + 1, contentRows: parseInt(sheet.data.maxRow) + 1}})
        );
        this.props.setForm(sheets[0]);
        //console.log(sheets);
        //this.owner.excel.clear();
//        this.owner.importExcel = true;
//        this.owner.drawSheets(sheets[0]);
//        if(0 && this.owner.owner.componentDidUpdate){
//        		//this.owner.owner.componentDidUpdate();
//        }
//         var hotSettings = {
// //            maxRows: excel["maxRow"],
// //            maxCols: excel["maxColumn"],
//             colWidths: excel["colWidths"],
//             rowHeights: excel["rowHeights"],
//             mergeCells: excel["mergeCells"],
//             customBorders: excel["customBorders"],
//         };
//         this.owner.excel.clear()
//         this.owner.excel.updateSettings(hotSettings)
//         this.owner.excel.setDataAtCell(excel["cellValue"]);
//         this.owner.excel.selectCell(0,0,0,0)
    }

    insertPictrue(insert_type, info){
		    if (info.file.status !== 'uploading') {
		    }
		    if (info.file.status === 'done') {
		    		let selected = this.owner.excel.getSelectedLast();
		    		if(insert_type === 1){
		    				let meta = this.owner.excel.getCellMeta(selected[0], selected[1]);
		    				if(!meta.def) meta.def = {}; 
		    				meta.def.image = info.file.response.data[0]['attach_url']['down'];
		    				this.owner.excel.setCellMeta(selected[0], selected[1], meta);
		    		}else{
				    		let {sheets, activeSheet} = this.owner.state;
				    		let images = activeSheet.images;
				    		if(!images) images = [];
				    		images.push({row: selected[1], col: selected[1], src: info.file.response.data[0]['attach_url']['down'], 'attach_id': info.file.response.data[0]['attach_id'], 'attach_name': info.file.response.data[0]['attach_name']});
				    		activeSheet.images = images;
				    		this.owner.setState({sheets, activeSheet});
		    		}
		    		this.owner.excel.render();
		    } else if (info.file.status === 'error') {
		        message.error(`${info.file.name} 导入失败！`);
		    }
    }
}

export default ReportOption;