import React, { Component } from 'react';
import { Tabs, Radio, InputNumber, Checkbox, Select, Modal, Button } from 'antd';
const Option = Select.Option;
const TabPane = Tabs.TabPane;

class DataTypeProperty extends Component {
    constructor(props) {
        super(props);
        this.owner = this.props.owner;
        this.state = {
            //默认选中key为1的项
            activeKey: '1',
            example: '1,234',
            decimal: '',
            checked: true,
            InputNumberValue: 0,
            selectValue: '',
            currency: '',
            dateSelectValue: '',
            InputCurrencyValue: 0,
        };
    }
    
    componentWillReceiveProps(nextProps) {
        let { dataType, dataFormat } = nextProps.def;
        if(!dataFormat) dataFormat = {format: '', culture: 'zh-CN'}
        let {format, culture} = dataFormat
        if(!dataType) dataType = 'text'

        switch (dataType) {
            case 'text':
                this.setState({
                    activeKey: '1',
                    example: '1,234',
                    decimal: '',
                    checked: true,
                    InputNumberValue: 0,
                    selectValue: '',
                    currency: '',
                    dateSelectValue: '',
                    InputCurrencyValue: 0,
                })
                break;
            case 'number':
                this.setState({
                    activeKey: '2',
                    InputNumberValue: format.indexOf('.') != -1 ? format.substr(format.indexOf('.') + 1).length : 0,
                    checked: format.indexOf(",") != -1,
                    example: format.indexOf(",") != -1 ? '1,234' : '1234',
                })
                this.onInputNumberChange(format.indexOf('.') != -1 ? format.substr(format.indexOf('.') + 1).length : 0)
                break;
            case 'date':
                this.setState({
                    activeKey: '3',
                    dateSelectValue: format
                })
                break;
            case 'currency':
                this.setState({
                    activeKey: '4',
                    InputCurrencyValue: format.indexOf('.') != -1 ? format.substr(format.indexOf('.') + 1).length : 0,
                    selectValue: culture=='zh-CN' ? "￥" : (culture=='en-US' ? "$" : "O")
                })
                this.InputCurrencyChange(format.indexOf('.') != -1 ? format.substr(format.indexOf('.') + 1).length : 0)
                break;
            default:
                this.resetState()
                break;
        }
    }
    //重置state
    resetState() {
        this.setState({
            activeKey: '1',
            example: '1,234',
            decimal: '',
            checked: true,
            InputNumberValue: 0,
            selectValue: '',
            currency: '',
            dateSelectValue: '',
            InputCurrencyValue: 0,
        })
    }
    handleOk = (e) => {
        let key = this.state.activeKey;
        let title = '';
        let obj = {};
        let fieldType = '', format = '', culture = 'zh-CN';
        let currConfigs = {};
        switch (key) {
            case '2':
                //数字格式小数位数
                obj.fixto = this.state.InputNumberValue
                fieldType = 'number'
                if (this.state.checked) {
                    //数字格式是否使用千位分隔符
                    obj.thousand = this.state.checked
                } else {
                    obj.thousand = false
                }
                if(obj.thousand){
                    format = '0,0';
                }else{
                    format = '0';
                }
                if(obj.fixto > 0){
                    format += '.'
                    let fixto = obj.fixto
                    while(fixto > 0){
                        format += '0'
                        fixto--
                    }
                }
                break;
            case '3':
                // title = '日期'
                fieldType = 'date'
                obj.date = this.state.dateSelectValue
                format = obj.date
                break;
            case '4':
                fieldType = 'currency'
                //金额格式使用千位分隔符
                obj.thousand = this.state.checked
                //金额格式小数位数
                obj.fixto = this.state.InputCurrencyValue
                //金额符号
                obj.currency = this.state.selectValue
                if(obj.currency === "O"){
                    obj.currency = "€"
                }

                if(obj.thousand){
                    format += '0,0';
                }else{
                    format += '0';
                }
                if(obj.fixto > 0){
                    format += '.'
                    let fixto = obj.fixto
                    while(fixto > 0){
                        format += '0'
                        fixto--
                    }
                }
                if(obj.currency === "￥"){
                    format = '$' + format
                }else if(obj.currency === "$"){
                    culture = 'en-US'
                    format = '$' + format
                }else {
                    culture = 'de-DE'
                    format += ' $'
                }
                break;
            default:
                fieldType = 'text'
                format = ''
                // title = '字符串'
                break;
        }

        this.props.setModalFlag(false);
        this.resetState();
        this.owner.owner.excel.getSelectedRange().map((cellRange) => {
            let {from, to} = cellRange;
            for (let row = from.row; row <= to.row; row++) {
                for (let col = from.col; col <= to.col; col++) {
                    let meta = this.owner.owner.excel.getCellMeta(row, col);
                    if(meta.def === undefined){
                        meta.def = {}
                    }
                    meta.def['dataType'] = fieldType
                    meta.def['dataFormat'] = {format: format, culture: culture}
                    this.owner.owner.excel.setCellMetaObject(row, col, meta);
                }
            }
        })
        this.owner.owner.excel.render()
    }
    handleCancel = (e) => {
        this.props.setModalFlag(false);
        this.resetState()
    }
    onTabClick = (key) => {
        // this.resetState();
        this.setState({
            activeKey: key
        })
    }
    //选择金额格式
    handleSelectChange = (value) => {
        switch (value) {
            case '￥':
                this.setState({
                    currency: '￥'
                })
                break;
            case '$':
                this.setState({
                    currency: '$'
                })
                break;
            case 'O':
                this.setState({
                    currency: '€'
                })
                break;
            default:
                this.setState({
                    currency: ''
                })
                break;
        }
        this.setState({
            selectValue: value
        })
    }
    //选择日期格式
    handleSelectDateChange = (value) => {
        this.setState({
            dateSelectValue: value
        })
    }
    InputCurrencyChange = (value) => {
        this.setState({
            InputCurrencyValue: value
        })
        switch (value) {
            case 0:
                this.setState({
                    decimal: ''
                })
                break;
            case 1:
                this.setState({
                    decimal: '0'
                })
                break;
            case 2:
                this.setState({
                    decimal: '10'
                })
                break;
            case 3:
                this.setState({
                    decimal: '210'
                })
                break;
            case 4:
                this.setState({
                    decimal: '3210'
                })
                break;
            case 5:
                this.setState({
                    decimal: '43210'
                })
                break;
            case 6:
                this.setState({
                    decimal: '543210'
                })
                break;
            default:
                break;
        }
    }
    //示例
    onInputNumberChange = (value) => {
        this.setState({
            InputNumberValue: value
        })
        switch (value) {
            case 0:
                this.setState({
                    decimal: ''
                })
                break;
            case 1:
                this.setState({
                    decimal: '0'
                })
                break;
            case 2:
                this.setState({
                    decimal: '10'
                })
                break;
            case 3:
                this.setState({
                    decimal: '210'
                })
                break;
            case 4:
                this.setState({
                    decimal: '3210'
                })
                break;
            case 5:
                this.setState({
                    decimal: '43210'
                })
                break;
            case 6:
                this.setState({
                    decimal: '543210'
                })
                break;
            default:
                break;
        }
    }
    //使用千分位分隔符
    onCheckboxChange = (e) => {
        e.target.checked ? this.setState({
            example: '1,234'
        }) : this.setState({
            example: '1234'
        })
        this.setState({
            checked: e.target.checked,
        });
    }
    render() {
        let {
            // setCurrentTag,
            // currentTag,
            // setCounterItemTags,
            // counterItemTags,
            modalFlag,
            setModalFlag
        } = this.props;
        return (
            <Modal title="字段格式" visible={modalFlag} onOk={this.handleOk.bind(this)} onCancel={this.handleCancel.bind(this)} footer={[
				            <Button key="back" type="ghost" onClick={this.handleCancel.bind(this)}>返 回</Button>,
				            <Button key="submit" type="primary" loading={this.state.loading} onClick={this.handleOk.bind(this)}>
				              确 定
				            </Button>
	          ]}>
                <div className="my-modal-content">
                    <Tabs
                        activeKey={this.state.activeKey}
                        tabPosition='left'
                        style={{ height: 320 }}
                        onTabClick={this.onTabClick}
                    >
                        <TabPane tab="字符串" key="1">以字符串展示，无任何特定格式</TabPane>
                        <TabPane tab="数字" key="2">
                            <ul style={{"list-style-type": "none"}}>
                                <li>小数位数:</li>
                                <li><InputNumber min={0} max={6} onChange={this.onInputNumberChange} value={this.state.InputNumberValue} /></li>
                                <li><Checkbox onChange={this.onCheckboxChange} checked={this.state.checked}>使用千位分隔符</Checkbox></li>
                                <li>
                                    示例：
                            </li>
                                <ul className="number-example" style={{"list-style-type": "none"}}>
                                    <li className="text-color-red">({this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''})</li>
                                    <li>({this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''})</li>
                                    <li className="text-color-red">{this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''}</li>
                                    <li>-{this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''}</li>
                                    <li className="text-color-red">-{this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''}</li>
                                </ul>
                            </ul>


                        </TabPane>
                        <TabPane tab="日期" key="3">
                            <ul style={{"list-style-type": "none"}}>
                                <li>日期格式将日期和时间序列数值显示为日期值</li>
                                <li>示例：</li>
                                <li>
                                    <Select value={this.state.dateSelectValue} style={{ width: 120 }} onChange={this.handleSelectDateChange}>
                                        <Option value="">无</Option>
                                        <Option value="yyyy/MM/dd">2012/3/14</Option>
                                        <Option value="yyyyMMdd">20120314</Option>
                                        <Option value="yyyy-MM-dd">2012-3-14</Option>
                                        <Option value="HH:mm:ss">13:30:55</Option>
                                        <Option value="HH:mm">13:30</Option>
                                        <Option value="yyyy-MM-dd HH:mm:ss">2012-3-14 13:30:55</Option>
                                    </Select>
                                </li>
                            </ul>
                        </TabPane>
                        <TabPane tab="金额" key="4">
                            <ul style={{"list-style-type": "none"}}>
                                <li>小数位数:</li>
                                <li><InputNumber min={0} max={6} onChange={this.InputCurrencyChange} value={this.state.InputCurrencyValue} /></li>
                                <li>
                                    货币符号：
                                <Select value={this.state.selectValue} style={{ width: 120 }} onChange={this.handleSelectChange}>
                                        <Option value="">无</Option>
                                        <Option value="￥">人民币(￥)</Option>
                                        <Option value="$">美元($)</Option>
                                        <Option value="O">欧元(€)</Option>
                                    </Select>
                                </li>
                                <li>
                                    示例：
                            </li>
                                <ul className="number-example" style={{"list-style-type": "none"}}>
                                    <li className="text-color-red">(
                                    {this.state.currency ? this.state.currency : ''}
                                        {this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''})
                                    </li>
                                    <li>(
                                    {this.state.currency ? this.state.currency : ''}
                                        {this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''})
                                    </li>
                                    <li className="text-color-red">
                                        {this.state.currency ? this.state.currency : ''}{this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''}
                                    </li>
                                    <li>
                                        -{this.state.currency ? this.state.currency : ''}{this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''}
                                    </li>
                                    <li className="text-color-red">
                                        -{this.state.currency ? this.state.currency : ''}{this.state.example}{this.state.decimal ? '.' + this.state.decimal : ''}
                                    </li>
                                </ul>
                            </ul>
                        </TabPane>
                    </Tabs>
                </div>
            </Modal>

        );
    }
}

export default DataTypeProperty