import {SET_FORM_ID, GET_FORM_DETAIL, SET_FORM_DETAIL_FLOW_ID, SET_FORM_DETAIL_IS_STOP, GET_FORMS_OF_APP, GET_COLUMNS_OF_FORM, SET_FORM_DETAIL_DATA, SET_FORM_FIELD_MAPS} from '../actions/form';

const formId = (state, action) => {
	if(state === undefined) {
		state = ''
	}

	switch(action.type) {
		case SET_FORM_ID:
			return action.formId || ''
		default:
			return state
	}
}

const formDetail = (state, action) => {
    if (state === undefined) {
        state = {}
    }
    
    switch(action.type){
    		case GET_FORM_DETAIL:
    				return {...state, ...action.form};
    		case SET_FORM_DETAIL_FLOW_ID:
    				return {...state, flowId: action.flowId};
    		case SET_FORM_DETAIL_IS_STOP:
    				return {...state, isStop: action.isStop};
    		case SET_FORM_DETAIL_DATA:
    				return {...state, formData: action.form};
    		case SET_FORM_FIELD_MAPS:	
    				return {...state, formData: {...state.formData, fieldMaps: action.fieldMaps}}			
    		default:
    				return state;
    }
}

const formsOfApp = (state, action) => {
		if(state === undefined){
				state = [];
		}
		
		switch(action.type){
				case GET_FORMS_OF_APP:
						return {...state, ...action.forms};
				default:
						return state;
		}
}

const columnsOfForm = (state, action) => {
		if(state === undefined){
				state = [];
		}
		
		switch(action.type){
				case GET_COLUMNS_OF_FORM:
						return {...state, ...action.columns};
				default:
						return state;
		}
}

const AllReducers = {
	 formId, formDetail, formsOfApp, columnsOfForm
}

export default AllReducers;