import React, { Component } from 'react';
import { Modal, Row, Col} from 'antd';

class XOptions extends Component {
		constructor(props){
	      super(props);
	      
	      this.state = {
	      		visible: false,
	      		xOptions: []	
	      }
	      this.owner = this.props.owner;
	  }
	  
	  componentWillReceiveProps(nextProps) {
	  		let {visible, xOptions} = nextProps;
	  		this.setState({visible, xOptions});
	  }
	  
	  changeOption(index, e){
	  		let {xOptions} = this.state;
	  		xOptions[index] = e.target.value;
	  		this.setState({xOptions});
	  }
	  
	  handleOk(){
	  		this.owner.setState({xOptions: this.state.xOptions, xOptionsVisible: false});
	  }
	  
	  handleCancel(){
	  		this.owner.setState({xOptionsVisible: false});
	  }
	  
	  render() {  		
		    let {xOptions} = this.state;
		    
		    return (
		      <div>
		        <Modal title="分类选项" visible={this.state.visible} onOk={this.handleOk.bind(this)} onCancel={this.handleCancel.bind(this)}>
		        		{
		        				xOptions.map((option, index) => (
		        						<Row key={index}><Col><input value={option} onChange={this.changeOption.bind(this, index)}/></Col></Row>
		        				))
		        		}
		        </Modal>
	        </div>
	      )
		}	  
}

export default XOptions;